/* ::Base Font Styles  */
/* ----------------------------------------------------------- */
*, html {
  -webkit-font-smoothing: antialiased; }

html {
  font-size: 14px; }
  @media (min-width: 768px) {
    html {
      font-size: 16px; } }
  @media (min-width: 1024px) {
    html {
      font-size: 17px; } }

a {
  text-decoration: none; }

strong {
  font-weight: bold; }

sup {
  vertical-align: super;
  font-size: smaller; }

/* ::Placeholder Text Styles  */
/* ----------------------------------------------------------- */
input:-moz-placeholder {
  /* Older versions of Firefox */ }

/* ::Objects Layer */
/* ----------------------------------------------------------- */
/* ::Components Layer */
/* ----------------------------------------------------------- */
/* ::Overides Layer */
/* ----------------------------------------------------------- */
/* ::Display related */
/* ----------------------------------------------------------- */
.t-large-only {
  display: none; }
  @media (min-width: 1024px) {
    .t-large-only {
      display: block; } }

.t-large-only-inline {
  display: none; }
  @media (min-width: 1024px) {
    .t-large-only-inline {
      display: inline; } }

.t-medium-only {
  display: none; }
  @media (min-width: 768px) {
    .t-medium-only {
      display: block; } }
  @media (min-width: 1024px) {
    .t-medium-only {
      display: none; } }

@media (min-width: 1024px) {
  .t-medium-down {
    display: none; } }

.t-small-only {
  display: none; }
  @media (min-width: 1024px) {
    .t-small-only {
      display: block; } }

/* ::Clearfixs and extends. Use which suits best */
/* ----------------------------------------------------------- */
.t-pie_clearfix:after {
  content: '';
  display: table;
  clear: both; }

.t-relative {
  position: relative; }

.t-fixed {
  position: fixed; }

.t-absolute-centered {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.t-absolute-bottom {
  position: absolute;
  top: auto !important;
  bottom: 0 !important; }

.t-center-children-vert {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

/* ::Colors */
/* ----------------------------------------------------------- */
.t-white {
  color: #ffffff; }

/* ::Text fixes */
/* ----------------------------------------------------------- */
.t-center-text {
  text-align: center; }

.t-font-smaller {
  font-size: 0.9em; }
